// 全局状态
const stateX = {
  id: 'main_主应用',
};
// 初始化 state
const actions = initGlobalState(stateX);
// 主项目项目监听和修改
actions.onGlobalStateChange((state, prev) => {
  // state: 变更后的状态; prev 变更前的状态
  console.log(state, prev);
});
// 将action对象绑到Vue原型上，为了项目中其他地方使用方便
Vue.prototype.$actions = actions;
