/** 手动加载一个微应用 */
const microApp1 = loadMicroApp(
  {
    name: 'vueapp',
    // entry: '//localhost:5501',
    entry: process.env.VUE_APP_SUB_VUE,
    container: '#container',
  },
  // 以 Shadow DOM 的形式嵌入微应用，以此来做到应用样式的真正隔离
  { sandbox: { strictStyleIsolation: true } }
)

